import { defineComponent } from "vue";
import { useStore } from "vuex";
let store;
export default defineComponent({
    name: "ConfirmPopUp",
    props: {
        content: {
            type: String,
            required: true,
        },
    },
    data() {
        return {};
    },
    created() {
        store = useStore();
    },
    methods: {
        cancelBtn() {
            store.dispatch("ConfirmPopUp/confirmPopUp", { showModal: false });
        },
    },
    computed: {
        setContent() {
            return store.getters["ConfirmPopUp/getContent"];
        }
    }
});
