import { defineComponent } from "vue";
import ConfirmPopUp from "@/views/PopUp/ConfirmPopUp.vue";
import { useStore } from "vuex";
import router from "@/router";
import { WNInterface } from "vue-wni";
let store;
export default defineComponent({
    name: "Alarm",
    data() {
        return {
            marketingStatusValue: 'Y',
            nightStatusValue: 'Y',
            marketingStatus: false,
            nightPushStatus: false,
            showModal: false,
            popUpContent: '',
            rider_email: '212',
            userId: '',
        };
    },
    async created() {
        store = useStore();
        //마케팅 설정 값 가져오기
        this.marketingStatusValue = store.getters["UserInfo/getMarketingPushValue"];
        this.nightStatusValue = store.getters["UserInfo/getNightPushValue"];
        if (this.marketingStatusValue === 'Y') {
            this.marketingStatus = true;
        }
        else {
            this.marketingStatus = false;
        }
        if (this.nightStatusValue === 'Y') {
            this.nightPushStatus = true;
        }
        else {
            this.nightPushStatus = false;
        }
        this.userId = await this.$storage.getItem("userId");
    },
    components: {
        ConfirmPopUp
    },
    computed: {
        // 팝업
        confirmPopUpShowModal() {
            return store.getters["ConfirmPopUp/getConfirmModal"];
        }
    },
    methods: {
        //뒤로가기 버튼
        backBtn() {
            router.back();
        },
        //수신동의
        async pushBtn() {
            if (!this.marketingStatus) {
                const VALUE = await store.dispatch("UserInfo/marketingPushUpdate", JSON.stringify({ userId: this.userId, pushNormalAcceptYn: 'Y' }));
                if (VALUE !== "error" && VALUE !== "tokenExpire" && VALUE !== "notToken") {
                    document.getElementById('push').setAttribute("checked", "true");
                    this.marketingStatus = true;
                    this.marketingStatusValue = 'Y';
                    this.popUpDialog("푸시 알림 동의가 변경되었습니다.");
                    await this.$storage.setItem('accessToken', VALUE.datas[0]['accessToken'], { expire: '30d' });
                }
                else if (VALUE === 'tokenExpire') {
                    const VALUE = await store.dispatch("ChangeToken/changeToken", JSON.stringify({ userId: this.userId }));
                    if (VALUE !== "error" && VALUE !== "notToken" && VALUE !== "tokenExpire") {
                        await this.pushBtn();
                    }
                    else {
                        await this.$router.replace({ name: 'Login' });
                    }
                }
                else {
                    this.popUpDialog("변경에 실패하였습니다.</br>잠시 후 다시 시도해주세요.");
                }
            }
            else {
                const VALUE = await store.dispatch("UserInfo/marketingPushUpdate", JSON.stringify({ userId: this.userId, pushNormalAcceptYn: 'N' }));
                if (VALUE !== "error" && VALUE !== "tokenExpire" && VALUE !== "notToken") {
                    document.getElementById('push').setAttribute("checked", "false");
                    this.marketingStatus = false;
                    this.marketingStatusValue = 'N';
                    this.popUpDialog("푸시 알림 동의가 변경되었습니다.");
                    await this.$storage.setItem('accessToken', VALUE.datas[0]['accessToken'], { expire: '30d' });
                }
                else if (VALUE === 'tokenExpire') {
                    const VALUE = await store.dispatch("ChangeToken/changeToken", JSON.stringify({ userId: this.userId }));
                    if (VALUE !== "error" && VALUE !== "notToken" && VALUE !== "tokenExpire") {
                        await this.pushBtn();
                    }
                    else {
                        await this.$router.replace({ name: 'Login' });
                    }
                }
                else {
                    this.popUpDialog("변경에 실패하였습니다.</br>잠시 후 다시 시도해주세요.");
                }
            }
        },
        //야간 수신 동의
        async nightPushBtn() {
            if (!this.nightPushStatus) {
                const VALUE = await store.dispatch("UserInfo/nightPushUpdate", JSON.stringify({ userId: this.userId, pushNightAcceptYn: 'Y' }));
                if (VALUE !== "error" && VALUE !== "tokenExpire" && VALUE !== "notToken") {
                    document.getElementById('nightPush').setAttribute("checked", "true");
                    this.nightPushStatus = true;
                    this.nightStatusValue = 'Y';
                    this.popUpDialog("야간 푸시 알림동의가 변경되었습니다.");
                    await this.$storage.setItem('accessToken', VALUE.datas[0]['accessToken'], { expire: '30d' });
                }
                else if (VALUE === 'tokenExpire') {
                    const VALUE = await store.dispatch("ChangeToken/changeToken", JSON.stringify({ userId: this.userId }));
                    if (VALUE !== "error" && VALUE !== "notToken" && VALUE !== "tokenExpire") {
                        await this.nightPushBtn();
                    }
                    else {
                        await this.$router.replace({ name: 'Login' });
                    }
                }
                else {
                    this.popUpDialog("변경에 실패하였습니다.</br>잠시 후 다시 시도해주세요.");
                }
            }
            else {
                const VALUE = await store.dispatch("UserInfo/nightPushUpdate", JSON.stringify({ userId: this.userId, pushNightAcceptYn: 'N' }));
                if (VALUE !== "error" && VALUE !== "tokenExpire" && VALUE !== "notToken") {
                    document.getElementById('nightPush').setAttribute("checked", "false");
                    this.nightPushStatus = false;
                    this.nightStatusValue = 'N';
                    this.popUpDialog("야간 푸시 알림 동의가 변경되었습니다.");
                    await this.$storage.setItem('accessToken', VALUE.datas[0]['accessToken'], { expire: '30d' });
                }
                else if (VALUE === 'tokenExpire') {
                    const VALUE = await store.dispatch("ChangeToken/changeToken", JSON.stringify({ userId: this.userId }));
                    if (VALUE !== "error" && VALUE !== "notToken" && VALUE !== "tokenExpire") {
                        await this.nightPushBtn();
                    }
                    else {
                        await this.$router.replace({ name: 'Login' });
                    }
                }
                else {
                    this.popUpDialog("변경에 실패하였습니다.</br>잠시 후 다시 시도해주세요.");
                }
            }
        },
        popUpDialog(content) {
            store.dispatch("ConfirmPopUp/confirmPopUp", { showModal: true, content: content });
            this.wnDialogShow();
        },
        async wnDialogShow() {
            await WNInterface.execute("wnDialogShow", {
                callback: WNInterface.cb(async (data) => {
                    if (data.status == "CLOSE") {
                        store.dispatch("ConfirmPopUp/confirmPopUp", { showModal: false });
                    }
                })
            });
        },
    }
});
